import React, {useRef, useState, useEffect} from 'react'
import useObserver from './useObserver'
import imgStart from './medias/imgStart.jpg'
import start from './medias/start.jpg'
import pho from './medias/pho.jpg'

export default function Starter({isFirst, classText, padding, imgDesktop, imgMobile}) {

    const deviceWidth = window.innerWidth

    const [contentRef,
        visible] = useObserver({root: null, rootMargin: "0px", treshold: 0.8})
    

    function setPosition() {
        let response = "3rem"
        isFirst
            ? response = "5rem"
            : response = "3rem"
        return response
    }

    return (
        <div
            ref={contentRef}
            style={{
            padding: `${padding}`
        }}
            className={`starter col-25 justify-center ${deviceWidth >= 767 ? "align-center" : "align-end"} mgbt-2 mgbt-s-4`}>
            <img className={`cover hidden ${visible && "reveal"}`} src={deviceWidth >= 767 ? imgDesktop : imgMobile} alt="img"/>
            <div className={`col-11 col-m-18 col-s-23 z1 justify-center mgbt-s-2 align-start flex-col light`}>
                <h1
                    style={{
                    fontSize: setPosition()
                }}
                    className={`text-xxl text-custom mgbt-text-10 hidden ${visible && "reveal"} ${classText}`}>SS21 Collection</h1>
                <p className={`text-s hidden ${visible && "reveal"}`}>But I must explain to you how all
                    this mistaken idea of denouncing pleasure and praising pain was born and I will
                    give you a complete account of the system, and expound the actual teachings of
                    the great explorer</p>
                <div className={`btn hidden ${visible && "reveal"}`}>Discover</div>
            </div>
        </div>
    )

}