import React, {useEffect, useState} from 'react';
import logo1 from './Icons/logo1.svg';
import logo2 from './Icons/logo2.svg';
import storelocator from './Icons/storelocator.svg';
import account from './Icons/account.svg';
import search from './Icons/search.svg';
import bag from './Icons/bag.svg';
import burger from './Icons/burger.svg';

function Nav() {

    const [blend,
        setBlend] = useState('');
    const [color,
        setColor] = useState('light');

    useEffect(() => {
        window
            .addEventListener('scroll', function () {
                window.scrollY > window.innerHeight - 116
                    ? setBlend('mix')
                    : setBlend('')
            })

    })

    return (
        <nav className={`col-23 justify-between navbar z8 ${blend}`}>
            <div className="col-3 col-m-19 col-s-14 main">
                <a href="#" className="logo">
                    <img src={logo1} className="logo1" alt="logo"/>
                    <img src={logo2} className="logo2" alt="logo"/>
                </a>
            </div>
            <div id="main-menu" className="offset-4 col-8 offset-m-3 col-m-10 main-menu">
                <ul className={`navigation ${color}`}>
                    <li className="navlink">
                        <a href="#">Men</a>
                    </li>
                    <li className="navlink">
                        <a href="#">Women</a>
                    </li>
                    <li className="navlink">
                        <a href="#">Kids</a>
                    </li>
                    <li className="navlink">
                        <a href="#">Customise</a>
                    </li>
                    <li className="navlink">
                        <a href="#">Lacoste inside</a>
                    </li>
                </ul>
            </div>
            <div className="col-1 col-m-2 col-s-4 search">
                <img src={search} className="icon" alt="search"/>

            </div>
            <div id="account" className="offset-4 col-2 offset-m-3 col-m-2 account">
                <img src={storelocator} className="icon" alt="storelocator"/>
                <img src={account} className="icon" alt="account"/>

            </div>
            <div className="col-1 col-s-2 basket">
                <img src={bag} className="icon" alt="bag"/>

            </div>
            <div id="menu" className="col-1 col-s-2 menu">
                <img style={{marginTop: '3px'}} src={burger} className="icon" alt="burger"/>
            </div>
        </nav>
    )
}

export default Nav