import React, {useRef, useState, useEffect} from 'react';
import useObserver from './useObserver'

export default function VisualStory({direction, img1, img2, title}) {

    const [ref1,
        visible1] = useObserver({root: null, rootMargin: "0px", treshold: 0.8})

    const [ref2,
        visible2] = useObserver({root: null, rootMargin: "0px", treshold: 0.8})

    const [ref3,
        visible3] = useObserver({root: null, rootMargin: "0px", treshold: 0.8})

    return (
        <div
            className={`visual-story col-21 col-s-25 justify-between align-center mgbt-2 mgbt-s-4`}
            style={{
            flexDirection: direction
        }}>
            <div ref={ref1} className={`col-11 visual-one hidden ${visible1 && "reveal"}`}>
                <img className="cover" src={img1} alt="img"/>
            </div>
            <div className="col-9 col-s-23 flex-col justify-self-start">
                <div ref={ref2} className={`visual-two hidden ${visible2 && "reveal"}`}>
                    <img className="cover" src={img2} alt="img"/>
                </div>
                <div
                    ref={ref3}
                    className={`flex flex-col justify-start align-start mgt-text-20 mgbt-text-30 hidden ${visible3 && "reveal"}`}>
                    <h1 className="text-l text-custom mgbt-text-10">{title}</h1>
                    <p className="text-s medium-dark">But I must explain to you how all this
                        mistaken idea of denouncing pleasure and praising pain was born and I will give
                        you a complete account of the system, and expound the actual teachings of the
                        great explorer</p>
                    <div className="btn">Discover</div>
                </div>
            </div>
        </div>
    )
}