import React, {useState, useEffect} from 'react'
import arrowRight from './Icons/arrowRight.svg'

export default function Password({updateAccess}) {

    const [val,
        setVal] = useState("")

    const [display,
        setDisplay] = useState("flex")

    const [error,
        setError] = useState(null)

    const mdp = "Lacoste2021!"

    function handleChange(e) {
        setVal(e.target.value)
        setError(null)
    }

    function errorMesage() {
        return (
            <p className="error-message">Wrong password, try again!</p>
        )
    }

    function handleSubmit(e) {
        e.preventDefault()
        if (val === mdp) {
            updateAccess(true);
            setDisplay('none');
        } else {
            setVal("")
            setError(errorMesage)
        }
    }

    return (
        <div style={{
            display: display
        }} className="password z10">
            <div className="col-8 col-m-13 col-s-23 flex-col align-start justify-center">
                <h1 className="text-m text-custom medium-light mgbt-text-30">Sign-in to see content</h1>
                <form
                    className="flex flex-col align-center justify-start"
                    onSubmit={handleSubmit}>
                    <input
                        type="password"
                        value={val}
                        onChange={handleChange}
                        placeholder="Password"/>
                    <button type="submit"><img className="icon" src={arrowRight}/></button>
                    {error}
                </form>
            </div>
        </div>
    )
}
